
'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useState, useEffect } from 'react';
import type { User } from '@/types';
import { fetchUserSessionForClient } from '@/app/actions';
import UserNavOrLogin from './UserNavOrLogin';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { 
  Home, 
  ScrollText, 
  LayoutDashboard, 
  PlusSquare, 
  ListChecks,
  FilePenLine, // Using FilePenLine for "作品管理"
  Trophy,
  Users, // For "用户管理"
  Settings, // For user settings icon
  Loader2
} from 'lucide-react';
import { cn } from '@/lib/utils';

interface NavItem {
  href: string;
  label: string;
  icon: React.ElementType;
  adminOnly?: boolean; // For links specific to admin panel
  staffOnly?: boolean; // For links requiring staff role
}

const publicNavItems: NavItem[] = [
  { href: '/', label: '首页', icon: Home },
  { href: '/history', label: '历史回顾', icon: ScrollText },
];

const dashboardNavItems: NavItem[] = [
  { href: '/dashboard', label: '仪表盘', icon: LayoutDashboard },
  { href: '/dashboard/submit', label: '提交作品', icon: PlusSquare }, // "创建活动" from screenshot
  { href: '/dashboard/my-projects', label: '我的作品', icon: ListChecks }, // "管理活动" from screenshot
  // { href: '/dashboard/settings', label: '用户管理', icon: Users }, // Placeholder for user settings
];

const adminNavItems: NavItem[] = [
  // { href: '/admin/dashboard', label: '管理看板', icon: LayoutDashboard, staffOnly: true }, // Placeholder
  { href: '/admin?view=works', label: '作品管理', icon: FilePenLine, staffOnly: true },
  { href: '/admin?view=competitions', label: '比赛管理', icon: Trophy, staffOnly: true },
  // { href: '/admin/users', label: '用户管理', icon: Users, staffOnly: true }, // Placeholder
];


export default function UnifiedSidebar() {
  const pathname = usePathname();
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loadingSession, setLoadingSession] = useState(true);

  useEffect(() => {
    async function getSession() {
      setLoadingSession(true);
      const user = await fetchUserSessionForClient();
      setCurrentUser(user);
      setLoadingSession(false);
    }
    getSession();
  }, []);

  const isDashboardPath = pathname.startsWith('/dashboard');
  const isAdminPath = pathname.startsWith('/admin');
  
  let navItemsToDisplay: NavItem[] = publicNavItems;

  if (isDashboardPath) {
    navItemsToDisplay = dashboardNavItems;
  } else if (isAdminPath && currentUser?.type === 'staff') {
    navItemsToDisplay = adminNavItems;
  } else if (isAdminPath && currentUser?.type !== 'staff') {
    // If on admin path but not staff, show minimal or redirect (handled by admin page itself)
    // For sidebar, maybe show nothing or a restricted message. For now, public links.
    navItemsToDisplay = publicNavItems; 
  }


  return (
    <aside className="w-64 bg-sidebar text-sidebar-foreground flex flex-col fixed top-0 left-0 h-screen border-r border-sidebar-border shadow-lg">
      <div className="p-4 border-b border-sidebar-border">
        <Link href="/" className="flex items-center gap-2">
          {/* <Blocks size={28} className="text-primary" /> */}
          <svg width="28" height="28" viewBox="0 0 100 100" className="text-primary fill-current">
            <rect width="100" height="100" rx="15" fill="hsl(var(--primary))"/>
            <rect x="15" y="15" width="30" height="30" fill="hsl(var(--background))"/>
            <rect x="55" y="15" width="30" height="30" fill="hsl(var(--background))"/>
            <rect x="15" y="55" width="30" height="30" fill="hsl(var(--background))"/>
            <rect x="55" y="55" width="30" height="30" fill="hsl(var(--sidebar-primary))"/> {/* Accent one block */}
          </svg>
          <h1 className="text-xl font-semibold text-sidebar-foreground">MineVent</h1>
          <Badge variant="outline" className="text-xs bg-primary/20 border-primary/50 text-primary px-1.5 py-0.5">Beta</Badge>
        </Link>
      </div>

      <nav className="flex-1 p-3 space-y-1.5 overflow-y-auto">
        {navItemsToDisplay.map((item) => {
          if (item.staffOnly && currentUser?.type !== 'staff' && isAdminPath) { // Ensure staff only links are hidden if not staff on admin path
            return null;
          }
          const isActive = item.href === '/' ? pathname === '/' : pathname.startsWith(item.href);
          return (
            <Link
              key={item.label}
              href={item.href}
              className={cn(
                "flex items-center gap-3 px-3 py-2.5 rounded-md text-sm font-medium transition-colors",
                isActive
                  ? "bg-sidebar-primary text-sidebar-primary-foreground shadow-sm"
                  : "text-sidebar-foreground hover:bg-sidebar-accent hover:text-sidebar-accent-foreground"
              )}
            >
              <item.icon className={cn("h-5 w-5", isActive ? "text-sidebar-primary-foreground" : "text-sidebar-foreground/80 group-hover:text-sidebar-accent-foreground")} />
              {item.label}
            </Link>
          );
        })}
      </nav>
      
      <Separator className="bg-sidebar-border" />
      <div className="p-3 border-t border-sidebar-border">
        {loadingSession ? (
          <div className="flex items-center justify-center h-10">
            <Loader2 className="h-5 w-5 animate-spin text-sidebar-foreground/60" />
          </div>
        ) : (
          <UserNavOrLogin currentUser={currentUser} />
        )}
      </div>
    </aside>
  );
}
