
'use server';

import { cookies } from 'next/headers';
import { redirect } from 'next/navigation';
import { APP_ID, APP_KEY, API_BASE_URL, SESSION_COOKIE_NAME } from '@/config';
import type { User, LoginUrlResponse, AuthCallbackResponse, Building, BuildingStatus, Competition, CompetitionStatus, BuildingSubmissionFormData } from '@/types';
import { mockBuildings } from '@/data/buildings';
import { mockCompetitions } from '@/data/competitions';
import { revalidatePath } from 'next/cache';
import { v4 as uuidv4 } from 'uuid'; // For generating unique IDs for new buildings

// 模拟数据库或持久化存储
let serverBuildings = [...mockBuildings];
const userVotes: Record<string, Set<string>> = {}; // { userId: Set<buildingId> }
let serverCompetitions = [...mockCompetitions];


export async function handleLoginRedirect(providerType: string) {
  const redirectUri = process.env.NODE_ENV === 'production' 
    ? 'YOUR_PRODUCTION_CALLBACK_URL' // TODO: Replace with actual production URL
    : `http://localhost:${process.env.PORT || 9002}/auth/callback`;

  const url = `${API_BASE_URL}/connect.php?act=login&appid=${APP_ID}&appkey=${APP_KEY}&type=${providerType}&redirect_uri=${encodeURIComponent(redirectUri)}`;

  try {
    const response = await fetch(url);
    if (!response.ok) {
      const errorData = await response.json().catch(() => ({ msg: '获取登录链接失败，响应无效。' }));
      console.error('Error fetching login URL:', errorData);
      return { error: errorData.msg || '获取登录链接失败' };
    }

    const data: LoginUrlResponse = await response.json();

    if (data.code === 0 && data.url) {
      redirect(data.url);
    } else {
      console.error('Error in login URL response:', data);
      return { error: data.msg || '获取登录链接时发生未知错误' };
    }
  } catch (error) {
    console.error('Network error fetching login URL:', error);
    return { error: '网络错误，无法连接到登录服务' };
  }
}

export async function processLoginCallback(type: string, code: string): Promise<{ success: boolean; error?: string }> {
  const url = `${API_BASE_URL}/connect.php?act=callback&appid=${APP_ID}&appkey=${APP_KEY}&type=${type}&code=${code}`;

  try {
    const response = await fetch(url);
    if (!response.ok) {
      const errorData = await response.json().catch(() => ({ msg: '处理回调失败，响应无效。' }));
      console.error('Error fetching user info:', errorData);
      return { success: false, error: errorData.msg || '处理回调失败' };
    }

    const data: AuthCallbackResponse = await response.json();

    if (data.code === 0 && data.social_uid && data.nickname && data.faceimg) {
      const user: User = {
        socialUid: data.social_uid,
        nickname: data.nickname,
        faceimg: data.faceimg,
        type: data.type || type,
        accessToken: data.access_token,
        gender: data.gender,
        location: data.location,
        ip: data.ip,
      };
      
      cookies().set(SESSION_COOKIE_NAME, JSON.stringify(user), {
        httpOnly: true,
        secure: process.env.NODE_ENV === 'production',
        maxAge: 60 * 60 * 24 * 7, // 1 week
        path: '/',
      });
      return { success: true };
    } else if (data.code === 2) {
      return { success: false, error: '登录未完成，请重试。' };
    } 
    else {
      console.error('Error in user info response:', data);
      return { success: false, error: data.msg || '获取用户信息时发生未知错误' };
    }
  } catch (error) {
    console.error('Network error fetching user info:', error);
    return { success: false, error: '网络错误，无法获取用户信息' };
  }
}

export async function handleStaffLogin(
  prevState: { error?: string } | undefined,
  formData: FormData
): Promise<{ error?: string } | void> {
  const username = formData.get('username') as string;
  const password = formData.get('password') as string;

  if (username === 'admin' && password === '123456') {
    const user: User = {
      socialUid: `staff_${username.toLowerCase()}`,
      nickname: '后台管理员 (STAFF)',
      faceimg: 'https://placehold.co/100x100.png?text=S', 
      type: 'staff',
    };

    cookies().set(SESSION_COOKIE_NAME, JSON.stringify(user), {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      maxAge: 60 * 60 * 24 * 7, 
      path: '/',
    });
    redirect('/'); 
  } else {
    return { error: '无效的员工帐户或密码。' };
  }
}

export async function handleLogout() {
  cookies().delete(SESSION_COOKIE_NAME);
  redirect('/');
}

export async function getUserSession(): Promise<User | null> {
  const sessionCookie = cookies().get(SESSION_COOKIE_NAME);
  if (sessionCookie?.value) {
    try {
      return JSON.parse(sessionCookie.value) as User;
    } catch (error) {
      console.error('Error parsing session cookie:', error);
      return null;
    }
  }
  return null;
}

// New Server Action callable from Client Components
export async function fetchUserSessionForClient(): Promise<User | null> {
  return await getUserSession();
}

export async function submitVote(buildingId: string): Promise<{ success: boolean; message: string; newVotes?: number }> {
  const user = await getUserSession();
  if (!user) {
    return { success: false, message: '请先登录再投票！' };
  }

  const buildingIndex = serverBuildings.findIndex(b => b.id === buildingId);
  if (buildingIndex === -1) {
    return { success: false, message: '建筑不存在！' };
  }

  if (serverBuildings[buildingIndex].status !== '有效') {
    return { success: false, message: '此作品当前状态无法投票。' };
  }

  if (!userVotes[user.socialUid]) {
    userVotes[user.socialUid] = new Set();
  }
  if (userVotes[user.socialUid].has(buildingId)) {
    return { success: false, message: '您已经投过票了！' };
  }

  serverBuildings[buildingIndex].votes += 1;
  userVotes[user.socialUid].add(buildingId);
  
  revalidatePath('/'); 
  revalidatePath('/history');
  revalidatePath(`/building/${buildingId}`); 
  revalidatePath('/dashboard/my-projects');

  return { success: true, message: '投票成功！', newVotes: serverBuildings[buildingIndex].votes };
}

export async function getRawBuildings(): Promise<Building[]> {
  return JSON.parse(JSON.stringify(serverBuildings)); 
}

export async function getBuildingById(id: string, currentUser?: User | null): Promise<Building | undefined> {
  let userToCheck = currentUser;
  if (!userToCheck) {
    userToCheck = await getUserSession(); // Fetch if not provided
  }
  const building = serverBuildings.find(b => b.id === id);
  if (building) {
    const buildingCopy = JSON.parse(JSON.stringify(building));
    if (userToCheck && userVotes[userToCheck.socialUid]) { 
      buildingCopy.currentUserHasVoted = userVotes[userToCheck.socialUid].has(id);
    } else {
      buildingCopy.currentUserHasVoted = false;
    }
    return buildingCopy;
  }
  return undefined;
}

export async function getActiveWorks(currentUser?: User | null): Promise<Building[]> {
  let userToCheck = currentUser;
  if (!userToCheck) {
    userToCheck = await getUserSession();
  }
  const competitions = await getCompetitions();
  const activeCompetitionIds = competitions
    .filter(comp => comp.status === '进行中')
    .map(comp => comp.id);

  const activeWorks = serverBuildings
    .filter(building => 
      building.competitionId && 
      activeCompetitionIds.includes(building.competitionId) &&
      building.status === '有效'
    )
    .sort((a, b) => b.votes - a.votes)
    .map(b => {
      const buildingCopy = JSON.parse(JSON.stringify(b));
      if (userToCheck && userVotes[userToCheck.socialUid]) { 
        buildingCopy.currentUserHasVoted = userVotes[userToCheck.socialUid].has(b.id);
      } else {
        buildingCopy.currentUserHasVoted = false;
      }
      return buildingCopy;
    });
  
  return activeWorks;
}

export type HistoricalCompetitionWithWorks = Competition & {
  works: Building[];
};

export async function getHistoricalWorksGroupedByCompetition(currentUser?: User | null): Promise<HistoricalCompetitionWithWorks[]> {
  let userToCheck = currentUser;
  if (!userToCheck) {
    userToCheck = await getUserSession();
  }
  const allCompetitions = await getCompetitions(); 
  const allBuildings = await getRawBuildings();   

  const historicalCompetitionsData = allCompetitions
    .filter(comp => comp.status !== '进行中')
    .sort((a, b) => {
      if (b.year !== a.year) return b.year - a.year;
      const dateA = new Date(a.startDate).getTime();
      const dateB = new Date(b.startDate).getTime();
      if (dateB !== dateA) return dateB - dateA;
      return a.name.localeCompare(b.name);
    });

  const groupedWorks = historicalCompetitionsData.map(comp => {
    const worksForCompetition = allBuildings
      .filter(building => building.competitionId === comp.id) 
      .sort((wa, wb) => wb.votes - wa.votes)
      .map(wb => {
        const buildingCopy = JSON.parse(JSON.stringify(wb));
        if (userToCheck && userVotes[userToCheck.socialUid]) { 
          buildingCopy.currentUserHasVoted = userVotes[userToCheck.socialUid].has(wb.id);
        } else {
          buildingCopy.currentUserHasVoted = false;
        }
        return buildingCopy;
      });
    return {
      ...comp, 
      works: worksForCompetition,
    };
  });
  return groupedWorks;
}


export async function updateBuildingStatus(buildingId: string, newStatus: BuildingStatus): Promise<{ success: boolean; message: string }> {
  const buildingIndex = serverBuildings.findIndex(b => b.id === buildingId);
  if (buildingIndex === -1) {
    return { success: false, message: '建筑不存在！' };
  }

  serverBuildings[buildingIndex].status = newStatus;
  
  revalidatePath('/'); 
  revalidatePath('/history');
  revalidatePath('/admin');
  revalidatePath(`/building/${buildingId}`);
  revalidatePath('/dashboard/my-projects');


  return { success: true, message: `作品 "${serverBuildings[buildingIndex].name}" 状态已更新为 "${newStatus}"。` };
}

export async function getCompetitions(): Promise<Competition[]> {
  return JSON.parse(JSON.stringify(serverCompetitions));
}

export async function getSubmittableCompetitions(): Promise<Competition[]> {
  const allComps = await getCompetitions();
  return allComps.filter(c => c.status === '进行中' || c.status === '策划中');
}


export async function createCompetition(
  competitionData: Omit<Competition, 'id' | 'startDate' | 'endDate'> & { startDate: string; endDate: string; }
): Promise<{ success: boolean; message: string; competition?: Competition }> {
  if (!competitionData.name || !competitionData.year) {
    return { success: false, message: '比赛名称和年份不能为空。' };
  }
  const newId = `comp${serverCompetitions.length + 1}_${Date.now()}`;
  const newCompetition: Competition = {
    id: newId,
    name: competitionData.name,
    year: competitionData.year,
    status: competitionData.status,
    startDate: competitionData.startDate,
    endDate: competitionData.endDate,
    description: competitionData.description,
  };
  serverCompetitions.unshift(newCompetition); 
  revalidatePath('/admin');
  revalidatePath('/');
  revalidatePath('/history');
  revalidatePath('/dashboard/submit'); // For competition list in submission form
  return { success: true, message: '比赛创建成功！', competition: newCompetition };
}

export async function updateCompetition(
  competitionId: string,
  competitionData: Partial<Omit<Competition, 'id' | 'startDate' | 'endDate'>> & { startDate?: string; endDate?: string; }
): Promise<{ success: boolean; message: string; competition?: Competition }> {
  const competitionIndex = serverCompetitions.findIndex(c => c.id === competitionId);
  if (competitionIndex === -1) {
    return { success: false, message: '比赛不存在！' };
  }
  serverCompetitions[competitionIndex] = {
    ...serverCompetitions[competitionIndex],
    ...competitionData,
  };
  revalidatePath('/admin');
  revalidatePath('/');
  revalidatePath('/history');
  revalidatePath('/dashboard/submit');
  return { success: true, message: '比赛更新成功！', competition: serverCompetitions[competitionIndex] };
}

export async function deleteCompetition(
  competitionId: string
): Promise<{ success: boolean; message: string }> {
  const initialLength = serverCompetitions.length;
  serverCompetitions = serverCompetitions.filter(c => c.id !== competitionId);
  if (serverCompetitions.length === initialLength) {
    return { success: false, message: '比赛不存在或删除失败！' };
  }
  revalidatePath('/admin');
  revalidatePath('/');
  revalidatePath('/history');
  revalidatePath('/dashboard/submit');
  return { success: true, message: '比赛删除成功！' };
}


export async function submitNewBuilding(
  formData: BuildingSubmissionFormData
): Promise<{ success: boolean; message: string; buildingId?: string }> {
  const currentUser = await getUserSession();
  if (!currentUser) {
    return { success: false, message: '请先登录才能提交作品。' };
  }

  const competition = serverCompetitions.find(c => c.id === formData.competitionId);
  if (!competition) {
    return { success: false, message: '选择的比赛无效。' };
  }

  const newBuilding: Building = {
    id: uuidv4(), // Generate a unique ID
    name: formData.name,
    author: currentUser.nickname,
    authorSocialUid: currentUser.socialUid,
    description: formData.description,
    imageUrl: formData.imageUrl || `https://placehold.co/800x600.png?text=${encodeURIComponent(formData.name)}`,
    aiHint: formData.aiHint,
    votes: 0,
    size: formData.size,
    territoryIdentifier: formData.territoryIdentifier,
    server: formData.server,
    coordinates: {
      x: formData.coordinateX,
      y: formData.coordinateY,
      z: formData.coordinateZ,
    },
    eventEdition: competition.year, // Assuming edition is same as year for simplicity
    eventYear: competition.year,
    status: '等待审核', // Default status
    competitionId: formData.competitionId,
    submissionDate: new Date().toISOString(),
  };

  serverBuildings.unshift(newBuilding); // Add to the beginning of the array

  revalidatePath('/');
  revalidatePath('/history');
  revalidatePath('/admin'); // Admin should see new submissions
  revalidatePath('/dashboard/my-projects');

  return { success: true, message: '作品提交成功，状态为等待审核。', buildingId: newBuilding.id };
}

export async function getUserSubmittedWorks(): Promise<Building[]> {
  const currentUser = await getUserSession();
  if (!currentUser) {
    return [];
  }

  const userWorks = serverBuildings
    .filter(building => building.authorSocialUid === currentUser.socialUid)
    .sort((a, b) => new Date(b.submissionDate!).getTime() - new Date(a.submissionDate!).getTime()) // Sort by newest first
    .map(b => {
      const buildingCopy = JSON.parse(JSON.stringify(b));
      if (currentUser && userVotes[currentUser.socialUid]) { 
         buildingCopy.currentUserHasVoted = userVotes[currentUser.socialUid].has(b.id);
      } else {
        buildingCopy.currentUserHasVoted = false;
      }
      return buildingCopy;
    });
    
  return userWorks;
}

    
