
'use client';

import Link from 'next/link';
import type { User } from '@/types';
import { Button } from '@/components/ui/button';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import LogoutButton from '@/components/auth/LogoutButton'; 
import { LogIn, UserCircle, LayoutDashboard, Settings, ChevronUp } from 'lucide-react'; // ChevronUp might be better than LogOutIcon here
import { cn } from '@/lib/utils';

interface UserNavOrLoginProps {
  currentUser: User | null;
  isSidebarCollapsed?: boolean;
}

export default function UserNavOrLogin({ currentUser, isSidebarCollapsed }: UserNavOrLoginProps) {
  if (currentUser) {
    const userRole = currentUser.type === 'staff' ? '管理员' : '用户';
    return (
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button 
            variant="ghost" 
            className={cn(
              "w-full flex items-center text-left hover:bg-sidebar-accent group h-auto py-2",
              isSidebarCollapsed ? "justify-center px-2" : "justify-between px-3"
            )}
          >
            <div className={cn("flex items-center gap-2 overflow-hidden", isSidebarCollapsed && "justify-center w-full")}>
              <Avatar className="h-8 w-8 border border-sidebar-border group-hover:border-sidebar-accent-foreground/50 shrink-0">
                <AvatarImage src={currentUser.faceimg} alt={currentUser.nickname} />
                <AvatarFallback className="bg-muted text-muted-foreground text-xs">
                  {currentUser.nickname ? currentUser.nickname.charAt(0).toUpperCase() : <UserCircle size={16}/>}
                </AvatarFallback>
              </Avatar>
              {!isSidebarCollapsed && (
                <div className="flex-1 overflow-hidden">
                  <p className="text-sm font-medium text-sidebar-foreground truncate group-hover:text-sidebar-accent-foreground">{currentUser.nickname}</p>
                  <p className="text-xs text-sidebar-foreground/70 truncate group-hover:text-sidebar-accent-foreground/80">{userRole}</p>
                </div>
              )}
            </div>
            {!isSidebarCollapsed && (
              <ChevronUp className="h-4 w-4 text-sidebar-foreground/70 group-hover:text-sidebar-accent-foreground transition-transform duration-200 group-data-[state=open]:rotate-180 shrink-0 ml-1" />
            )}
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent 
          className="w-56 mb-1 bg-popover border-border shadow-xl" 
          align={isSidebarCollapsed ? "center" : "end"}
          side="top" 
          sideOffset={8}
          forceMount
        >
          <DropdownMenuLabel className="font-normal text-popover-foreground">
            <div className="flex flex-col space-y-1">
              <p className="text-sm font-medium leading-none">{currentUser.nickname}</p>
              <p className="text-xs leading-none text-muted-foreground">
                {currentUser.type === 'staff' ? 'STAFF 账户' : `${currentUser.type ? currentUser.type.toUpperCase() : '未知'} 用户`}
              </p>
            </div>
          </DropdownMenuLabel>
          <DropdownMenuSeparator className="bg-border" />
          {currentUser.type !== 'staff' && (
            <DropdownMenuItem asChild className="cursor-pointer text-popover-foreground hover:!bg-accent hover:!text-accent-foreground">
              <Link href="/dashboard">
                <LayoutDashboard className="mr-2 h-4 w-4" />
                <span>仪表盘</span>
              </Link>
            </DropdownMenuItem>
          )}
          {currentUser.type === 'staff' && (
             <DropdownMenuItem asChild className="cursor-pointer text-popover-foreground hover:!bg-accent hover:!text-accent-foreground">
              <Link href="/admin">
                <Settings className="mr-2 h-4 w-4" />
                <span>管理后台</span>
              </Link>
            </DropdownMenuItem>
          )}
          <DropdownMenuSeparator className="bg-border"/>
          <LogoutButton /> 
        </DropdownMenuContent>
      </DropdownMenu>
    );
  }

  return (
    <Button 
      asChild 
      variant="ghost" 
      className={cn(
        "w-full justify-start px-3 py-2.5 hover:bg-sidebar-accent group h-10", // Ensure consistent height
        isSidebarCollapsed && "justify-center p-0" // Icon only needs p-0 or specific icon padding
      )}
    >
      <Link 
        href="/login" 
        className={cn(
            "flex items-center gap-3 text-sidebar-foreground group-hover:text-sidebar-accent-foreground", 
            isSidebarCollapsed && "w-full h-full justify-center p-2.5" // Ensure link fills button and centers icon
        )}
        title={isSidebarCollapsed ? "登录 / 注册" : ""}
      >
        <LogIn className={cn("h-5 w-5 shrink-0")} /> {/* Removed mx-auto for collapsed state, parent handles centering */}
        {!isSidebarCollapsed && <span className="text-sm font-medium whitespace-nowrap">登录 / 注册</span>}
      </Link>
    </Button>
  );
}

